.content {
    text-align: center;
    margin-bottom: 30px;
  }
  .header {
    font-size: 42px;
  }
  .img {
    display: flex;
    margin: 0 auto;
    width: 90%;
  }
  .container {
    margin: 0 auto;
    width: 75%;
    height: 80vh;
    border-color: white;
  }
  .title {
    width: 70%;
    font-size: 30px;
    padding: 20px;
    border-radius: 5px;
    font-size: 33px;
  }
  .description {
    width: 80%;
    font-size: 25px;
  }

  

.slick-dots{
    display: none !important;
}